*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

:root {
  --pimary-accent-color: #d41443;
  --secondary-accent-color: #ffa5ba;
  --primary-bg-color: #fff;
  --secondary-bg-color: #fff5f6;
  --hero-bg-color: #ffb8ca;
  --card1-bg-color: #ffb8ca;
  --card2-bg-color: #f0d1a5;
  --card3-bg-color: #c2e297;
  --mobile-menu-bg-color: #e17992;
  --btn-hover-bg: #ecebeb;
  --primary-text-color: #000;
  --secondary-text-color: #907e82;
  --tertiary-text-color: #fff;
  --accent-font: "Titan One", cursive;
  --main-font: "DM Sans", sans-serif;
  --typical-time-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  background-color: var(--primary-bg-color);
  color: var(--primary-text-color);
  font-family: var(--main-font);
  font-size: 16px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.title {
  font-family: var(--accent-font);
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--pimary-accent-color);
  font-size: 34px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 48px;
    line-height: 1.14;
  }
}

@media screen and (min-width: 1200px) {
  .title {
    letter-spacing: .06em;
    font-size: 58px;
    line-height: 1.14;
  }
}

.label {
  font-family: var(--accent-font);
  text-align: center;
  text-transform: uppercase;
  color: var(--secondary-accent-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.16;
}

@media screen and (min-width: 768px) {
  .label {
    font-size: 26px;
    font-weight: 400;
    line-height: 1.15;
  }
}

@media screen and (min-width: 1200px) {
  .label {
    letter-spacing: .04em;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.13;
  }
}

.btn {
  text-align: center;
  border: none;
  border-radius: 22px;
  align-items: center;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.btn--white {
  background-color: var(--primary-bg-color);
  color: var(--pimary-accent-color);
}

.btn--red {
  background-color: var(--pimary-accent-color);
  color: var(--tertiary-text-color);
}

.no-scroll {
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.header {
  background-color: var(--hero-bg-color);
  outline: rgba(0, 0, 0, 0);
  padding-top: 8px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header {
    outline: rgba(0, 0, 0, 0);
    padding-top: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    outline: rgba(0, 0, 0, 0);
    padding-top: 37px;
  }
}

.header__logo {
  margin-right: auto;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .header__logo {
    margin-right: 125px;
  }
}

.header__btn {
  background-color: var(--primary-bg-color);
  color: var(--pimary-accent-color);
  transition-property: background-color, color, box-shadow;
  transition-duration: .25s;
  transition-timing-function: var(--typical-time-function);
  margin-left: 32px;
  padding: 13px 37px;
  display: none;
}

@media screen and (min-width: 768px) {
  .header__btn {
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .header__btn {
    margin-left: 0;
  }
}

.header__btn:hover, .header__btn:focus {
  background-color: var(--pimary-accent-color);
  color: var(--tertiary-text-color);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

nav {
  justify-content: center;
  align-items: center;
  display: flex;
  left: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-list {
  z-index: 2;
  justify-content: center;
  align-items: center;
  display: none;
}

@media screen and (min-width: 1200px) {
  .header-list {
    display: flex;
  }
}

.header-list__item {
  margin-right: 35px;
}

.header-list__item:last-child {
  margin-right: 0;
}

.header-list__link {
  color: var(--tertiary-text-color);
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: var(--typical-time-function);
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.header-list__link.current, .header-list__link:hover, .header-list__link:focus {
  color: var(--pimary-accent-color);
}

.menu-open-btn {
  background-color: var(--hero-bg-color);
  color: var(--pimary-accent-color);
  border: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin-left: auto;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .menu-open-btn {
    display: none;
  }
}

.menu-open-icon {
  fill: currentColor;
}

.menu-open-icon:hover, .menu-open-icon:focus {
  fill: var(--tertiary-text-color);
}

.mob-menu {
  z-index: 999;
  width: 78%;
  height: 100vh;
  background-color: var(--mobile-menu-bg-color);
  transition: transform 300ms var(--typical-time-function), visibility 300ms var(--typical-time-function), opacity 300ms var(--typical-time-function);
  padding-top: 60px;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(0);
}

@media screen and (min-width: 768px) {
  .mob-menu {
    width: 40%;
    left: 60%;
  }
}

@media screen and (min-width: 1200px) {
  .mob-menu {
    display: none;
  }
}

.mob-menu__btn {
  background-color: var(--primary-bg-color);
  color: var(--pimary-accent-color);
  transition-property: background-color, color, box-shadow;
  transition-duration: .25s;
  transition-timing-function: var(--typical-time-function);
  margin-bottom: 19px;
  margin-left: auto;
  margin-right: auto;
  padding: 13px 48px;
}

.mob-menu__btn:hover, .mob-menu__btn:focus {
  background-color: var(--pimary-accent-color);
  color: var(--tertiary-text-color);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

@media screen and (min-width: 1200px) {
  .mob-menu {
    display: none;
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateX(100%);
}

.no-scroll {
  overflow: hidden;
}

.icon-btn-arrow {
  fill: currentColor;
  margin-left: 16px;
}

.icon-btn-arrow:hover, .icon-btn-arrow:focus {
  fill: var(--tertiary-text-color);
}

.mob-menu-list__item {
  border-bottom: 1px solid #e18298;
  padding-top: 11px;
  padding-bottom: 11px;
}

.mob-menu-list__item:first-child {
  border-top: 1px solid #e18298;
}

.mob-menu-list__link {
  color: var(--tertiary-text-color);
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: var(--typical-time-function);
  padding-left: 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
}

.mob-menu-list__link.current, .mob-menu-list__link:hover, .mob-menu-list__link:focus {
  color: var(--pimary-accent-color);
}

.menu-close-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .menu-close-btn {
    right: 32px;
  }
}

.menu-close {
  fill: #fff;
}

.menu-close:hover, .menu-close:focus {
  fill: var(--pimary-accent-color);
}

