.container {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;

  // @include mobile-responsive {
  //   max-width: 480px;
  // }

  @include mobile {
    width: 480px;
  }

  @include tablet {
    width: 768px;
    padding-left: 16px;
    padding-right: 16px;
  }

  @include desktop {
    width: 1200px;
  }
}
